import React from 'react';
import {useParams} from 'react-router-dom';
import {
    useGetModuleGroups,
    useGetModules
} from "../../Providers/CourseProvider";
import Section from "../../Atomics/Section";
import CourseCard from "../../CourseCard";
import CourseCarousel from "../../CourseCarousel";

const ModuleGroup = ({courseId, moduleGroupId}) => {
    const modules = useGetModules(courseId, moduleGroupId);

    console.log(courseId, moduleGroupId);

    const constructModules = () => {
        const temp = [];

        for(let key in modules) {
            const {title, description, symbol} = modules[key];

            temp.push(
                <CourseCard key={key} title={title} symbol={symbol} to={`/course/${courseId}/${moduleGroupId}/${key}`}>
                    {description}
                </CourseCard>
            );
        }

        return temp;
    };

    return constructModules();
}

const Course = () => {
    const {courseId} = useParams();
    const moduleGroups = useGetModuleGroups(courseId);

    const constructModuleGroups = () => {
        const temp = [];

        for(let key in moduleGroups) {
            const {title} = moduleGroups[key];

            temp.push(
                <Section key={key} title={title}>
                    <CourseCarousel style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center'}}>
                        <ModuleGroup moduleGroupId={key} courseId={courseId} />
                    </CourseCarousel>
                </Section>
            )
        }

        return temp;
    };

    return constructModuleGroups();
};

export default Course;